/* Стили для Загрузки файла */
.FileInputWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 140px;
  border: 1px dashed #3F8AE0;
}

/* Стили для Превью загруженного фото */
.ImagePreview {
  overflow: hidden;
  position: relative;
  border-radius: 10px;
  height: 140px;
  margin-left: 12px;
  margin-right: 12px;
  border-radius: 10px;
}

.ImagePreview > img {
  width: 100%;
  height: 140px;
  object-fit: cover;
}

.ImagePreview > button {
  position: absolute;
  right: 8px;
  top: 8px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  border: 2px solid #fff;
  background-color: #99A2AD;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Стили для Сниппета */
.PostSnippet {
  overflow: hidden;
  width: 100%;
  box-sizing: border-box;
}

.PostSnippetContent {
  overflow: hidden;
  border-top-left-radius: 10;
  border-top-right-radius: 10;
  border: 1px solid #EEE;
  border-radius: 10px;
}

.PostSnippetContent > img {
  width: 100%;
  height: auto;
}

.PostSnippetHeading {
  padding: 8px 12px;
  border-bottom: 1px solid #EEE;
}

.PostSnippetCaption {
  color: #818C99;
}

.PostSnippetProgressValue {
  margin-bottom: 4px;
}

.PostSnippetProgress {
  display: flex;
  justify-content: space-between;
  padding: 12px;
}

.PostSnippetProgress.Fixed {
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  position: fixed;
  bottom: 0;
}

.PostSnippetProgress.Fixed .Progress__in {
  --accent: #4bb34b
}

.PostSnippetProgressWrapper {
  flex-grow: 1;
  margin-right: 12px;
}

/* Стили для Поста */
.Post {}

.PostPreview {}

.PostPreview > img {
  width: 100%;
  height: auto;
}

/* Стили для ProgressBar */
.ProgressBar {
  height: 24px;
  position: relative;
  border-radius: 6px;
  background-color: #F9F9FB;
}

.ProgressBarFilled {
  height: 24px;
  position: absolute;
  border-radius: 6px;
  top: 0;
  background-color: #4BB34B;
}

.ProgressBarValue {
  position: absolute;
  font-weight: 500;
  font-size: 14px;
}

.ProgressBarCurrentValue {
  z-index: 2;
  color: #FFFFFF;
  top: 3px;
}

.ProgressBarTargetValue {
  top: 3px;
  right: 8px;
  color: #818C99;
}
